






















































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import AuditRepository from '@/services/repositories/audit'
import getRepository from '@/services'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import { PaginationInterface } from '@/interface'
import ContractRepository from '@/services/repositories/contract'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class TestPopup extends Mixins(PopupMixin) {
  @Prop({ required: true, type: Object, default: {} }) query!: Record<string, any>
  @Prop({ default: [] }) dataSelect?: Array<Record<string, any>>

  @beUser.State('user') user?: Record<string, any>
  total: number | string = 0
  api?: AuditRepository
  apiContract?: ContractRepository
  data: Record<string, any> = {}
  summary: Record<string, any> = {}
  isLoading = false
  tableData = []
  updateValue = {
    newAuditStatus: 6
  }
  pageQuery: PaginationInterface = {
    page: 1,
    search: '',
    limit: 20,
    total: 0,
    orderByClause: ''
  }
  selectQuery: PaginationInterface = {
    page: 1,
    search: '',
    limit: 20,
    total: 0,
    orderByClause: ''
  }
  newquery: any = {
    areaIdArray: [],
    branchIdArray: [],
    transOfficeIdArray: [],
    salesMemberIdArray: [],
    consultantMemberIdArray: [],
    refMemberIdArray: [],
    productIdArray: [],
    paymentPeriod: '',
    currentContractYear: '',
    currentFeeYear: '',
    numOfContractYear: '',
    salesChannel: '',
    consultantType: '',
    customerType: '',
    contractStatus: '',
    expireStatus: '',
    auditStatus: 3,
    fromApplyFormDate: '',
    toApplyFormDate: '',
    fromIssueDate: '',
    toIssueDate: '',
    fromLastPayDate: '',
    toLastPayDate: '',
    fromExpireDate: '',
    toExpireDate: '',
    fromBasicAmount: '',
    toBasicAmount: '',
    fromExtraAmount: '',
    toExtraAmount: '',
    fromTotalAmount: '',
    toTotalAmount: ''
  }
  params = {
    searchType: 'CONTRACT',
    auditStatus: '3',
    page: 1,
    limit: 20
  }
  newparams = {
    searchType: 'CONTRACT',
    auditStatus: '3'
  }
  dataInput = {
    keywordString: '',
    areaIdArray: [],
    branchIdArray: '',
    transOfficeIdArray: '',
    salesMemberIdArray: '',
    consultantMemberIdArray: '',
    refMemberIdArray: '',
    productIdArray: '',
    paymentPeriod: '',
    currentContractYear: '',
    currentFeeYear: '',
    numOfContractYear: '',
    salesChannel: '',
    consultantType: '',
    customerType: '',
    contractStatus: '',
    expireStatus: '',
    auditStatus: 2,
    fromApplyFormDate: '',
    toApplyFormDate: '',
    fromIssueDate: '',
    toIssueDate: '',
    fromLastPayDate: '',
    toLastPayDate: '',
    fromExpireDate: '',
    toExpireDate: '',
    fromBasicAmount: '',
    toBasicAmount: '',
    fromExtraAmount: '',
    toExtraAmount: '',
    fromTotalAmount: '',
    toTotalAmount: ''
  }
  newData = {}
  pageInfor = {
    total: 1000,
    page: 1,
    limit: 10
  }
  isBtnLoading = false
  async handleConfirm(): Promise<void> {
    this.isBtnLoading = true

    if (this.query) {
      this.query.auditStatus = this.$route.name == 'yeu-cau-hieu-chinh' ? '5' : '3'
      this.query.newAuditStatus = this.$route.name == 'yeu-cau-hieu-chinh' ? '7' : '6'
      this.query.limit = ''
      this.query.page = ''
    }
    if (this.dataSelect && this.dataSelect.length > 0) {
      const bankContractNo = this.dataSelect.map((item: any) => item.bankContractNo).join(',')
      const contractNo = this.dataSelect.map((item: any) => item.contractNo).join(',')
      await this.api
        ?.updateAuditSelect({
          bankContractNoArray: bankContractNo,
          auditStatus: this.$route.name == 'yeu-cau-hieu-chinh' ? 7 : 6,
          contractNoArray: contractNo
        })
        .then(() => {
          this.tableData = []
          this.$message.success(`${this.$route.name == 'yeu-cau-hieu-chinh' ? 'Từ chối hiệu chỉnh thành công' : 'Từ chối duyệt thành công'}`)
        })
        .catch(() => {
          this.$message.warning(`${this.$route.name == 'yeu-cau-hieu-chinh' ? 'Từ chối hiệu chỉnh thất bại' : 'Yêu cầu phê duyệt thất bại'}`)
        })
    } else {
      await this.api
        ?.updateAuditFilter(this.query)
        .then(() => {
          this.tableData = []
          this.$message.success(`${this.$route.name == 'yeu-cau-hieu-chinh' ? 'Từ chối hiệu chỉnh thành công' : 'Từ chối duyệt thành công'}`)
        })
        .catch(() => {
          this.$message.warning(`${this.$route.name == 'yeu-cau-hieu-chinh' ? 'Từ chối hiệu chỉnh thất bại' : 'Từ chối duyệt thất bại'}`)
        })
    }

    this.setOpenPopup({
      popupName: 'cancel-popup',
      isOpen: false
    })
    this.isBtnLoading = false

    this.$emit('update')
  }
  handleSelection(): void {
    return
  }
  handleSizeChange(value: number): void {
    this.params.page = 1
    this.params.limit = value
    this.pageQuery.page = 1
    this.pageQuery.limit = value
    this.selectQuery.page = 1
    this.selectQuery.limit = value

    this.setData()
  }
  handleCurrentChange(value: number): void {
    this.params.page = value
    this.setData()
  }
  checkLength(value: string): string {
    if (value.split(' ').length > 5) {
      return value.split(' ').splice(0, 4).join(' ') + '...'
    }
    return value
  }
  getDataSumary(): void {
    if (this.dataSelect && this.dataSelect.length > 0) {
      const bankContractNo = this.dataSelect.map((item: any) => item.bankContractNo).join(',')
      const contractNo = this.dataSelect.map((item: any) => item.contractNo).join(',')
      this.apiContract
        ?.getSalaryContractSelect({
          bankContractNoArray: bankContractNo,
          contractNoArray: contractNo
        })
        .then((response: any) => {
          this.summary = response.data
        })
        .catch(() => (this.isLoading = false))
    } else {
      this.apiContract
        ?.getSalaryContract(this.query)
        .then((response: any) => {
          this.summary = response.data
        })
        .catch(() => (this.isLoading = false))
    }
  }
  setData(): void {
    if (this.dataSelect && this.dataSelect.length > 0) {
      this.selectQuery.total = this.dataSelect.length
    }
    this.query.limit = this.params.limit
    this.query.page = this.params.page
    this.isLoading = true
    this.apiContract
      ?.getData(this.query)
      .then((list: Record<string, any>) => {
        this.data = list
        this.tableData = this.data.data
        this.isLoading = false
        this.pageInfor.total = list.total
      })
      .catch(() => (this.isLoading = false))

    this.apiContract
      ?.getContractCount(this.query)
      .then(count => {
        this.pageQuery.total = count.data
        this.isLoading = false
      })
      .catch(() => (this.isLoading = false))
  }
  // mounted(): void {
  //   this.setData(this.params)
  // }

  init(): void {
    this.isLoading = false
  }
  created(): void {
    this.api = getRepository('audit', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.init()
    this.isLoading = false
  }
  handleOpen(): void {
    this.setData()
    this.getDataSumary()
    // console.log('query', this.query)
    this.newquery = {
      ...this.query,
      ...this.updateValue
    }
    // console.log(this.newquery)
  }
  handleClose(): void {
    // this.dataSelect = []
    this.$emit('reset')
  }
  handleChangeAction(): void {
    return
  }
  handleFilter(): void {
    return
  }
  filters(): void {
    // console.log(1)
  }
}
