































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { RELATIONSHIP_TYPE } from '@/configs'
import getRepository from '@/services'
import PopupMixin from '@/mixins/popup'
import AuditRepository from '@/services/repositories/audit'
import AddCustomer from './AddCustomer.vue'
import { namespace } from 'vuex-class'
import ContractRepository from '@/services/repositories/contract'
import EmployeeRepository from '@/services/repositories/employee'
import CustomerRepository from '@/services/repositories/customer'
import { forEach, trim } from 'lodash'
import EventBus from '@/utils/eventBus'

const beUser = namespace('beUser')

@Component({ components: { AddCustomer } })
export default class AuditPopupAction extends Mixins(PopupMixin) {
  @Prop({ required: false, default: {} }) data: any
  @Prop({ required: false, type: Array, default: [] }) dataSelect!: Array<Record<string, any>>
  @Prop() checkCustomerType: any
  @beUser.State('user') user?: Record<string, any>
  api!: AuditRepository
  apiContract?: ContractRepository
  apiMember?: EmployeeRepository
  apiCustomer?: CustomerRepository
  show = false
  valueSelect = ''
  selectOptions: any = []
  listOptions: any = []
  loading = false
  dataSearch: any
  queryDrop = {
    i: 0,
    ps: 20,
    search: ''
  }
  optionsRelationship = []
  emailSelect = ''
  phoneSelect = ''

  valueRelationship = ''

  handleItemClick(value: any[]): void {
    const item = this.dataSearch.find((item: Record<string, any>) => item.id == value)
    if (item) {
      if (this.data.code == 'customerCif') {
        this.emailSelect = item.customerEmail
        this.phoneSelect = item.customerMobilePhone
      } else {
        this.emailSelect = item.email
        this.phoneSelect = item.phone
      }
    }
  }
  handleOpen(): void {
    console.log('checkCustomerType', this.checkCustomerType)
    this.queryDrop.search = ''
    this.queryDrop.i = 0
    if (this.data.optionsRelationship) {
      this.optionsRelationship = this.data.optionsRelationship?.data
    }
    this.loadDataSearch(this.data.code)
      .then((res: any) => {
        this.listOptions = res
        this.selectOptions = this.listOptions.filter((item: Record<string, any>) => {
          return item.label.toLowerCase().indexOf(this.queryDrop.search.toLowerCase()) > -1
        })
      })
      .catch(err => {
        console.log(err)
      })
  }
  remoteMethod(search: string): void {
    this.queryDrop.search = trim(search)
    this.queryDrop.i = 0
    this.loadDataSearch(this.data.code)
      .then((res: any) => {
        this.listOptions = res
        this.selectOptions = this.listOptions.filter((item: Record<string, any>) => {
          return item.label.toLowerCase().indexOf(this.queryDrop.search.toLowerCase()) > -1
        })
      })
      .catch(err => {
        console.log(err)
      })
  }
  loadMore(): void {
    if (this.data.code === 'customerCif') {
      if (this.queryDrop.i == 0) this.queryDrop.i = 1
      this.queryDrop.i += 1
    } else {
      this.queryDrop.i += this.queryDrop.ps
    }
    this.loadDataSearch(this.data.code, true)
      .then((res: any) => {
        this.listOptions = res
        this.selectOptions = this.listOptions.filter((item: Record<string, any>) => {
          return item.label.toLowerCase().indexOf(this.queryDrop.search.toLowerCase()) > -1
        })
      })
      .catch(err => {
        console.log(err)
      })
  }

  async loadDataSearch(popupType: string, loadMore = false): Promise<Array<Record<string, unknown>>> {
    this.loading = true
    let listOptions = []
    switch (popupType) {
      case 'advisoryStaff':
        await this.apiMember
          ?.getEmployeeList({
            search: trim(this.queryDrop.search),
            groupCode: 'INSURANCE',
            i: this.queryDrop.i,
            ps: this.queryDrop.ps
          })
          .then(list => {
            if (loadMore) {
              if (list.data.length > 0) {
                this.dataSearch = [...this.dataSearch, ...list.data]
              }
            } else {
              this.dataSearch = list.data
            }
          })
          .catch(error => console.log(error))
        listOptions = this.dataSearch.map((item: Record<string, any>) => {
          return { value: `${item.id}`, label: `${item.memberNo ? item.memberNo : ''}${item.memberNo && item.fullName ? ' - ' : ''}${item.fullName ? item.fullName : ''}` }
        })
        break
      case 'introStaff':
        await this.apiMember
          ?.getEmployeeList({
            search: trim(this.queryDrop.search),
            groupCode: 'INSURANCE',
            i: this.queryDrop.i,
            ps: this.queryDrop.ps
          })
          .then(list => {
            if (loadMore) {
              if (list.data.length > 0) {
                this.dataSearch = [...this.dataSearch, ...list.data]
              }
            } else {
              this.dataSearch = list.data
            }
          })
          .catch(error => console.log(error))
        listOptions = this.dataSearch.map((item: Record<string, any>) => {
          return { value: `${item.id}`, label: `${item.memberNo ? item.memberNo : ''}${item.memberNo && item.fullName ? ' - ' : ''} ${item.fullName ? item.fullName : ''}` }
        })
        break
      case 'customerCif':
        await this.apiCustomer
          ?.getCustomers({
            search: trim(this.queryDrop.search),
            page: this.queryDrop.i,
            size: this.queryDrop.ps
          })
          .then(list => {
            if (loadMore) {
              if (list.data.content.length > 0) {
                this.dataSearch = [...this.dataSearch, ...list.data.content]
              }
            } else {
              this.dataSearch = list.data.content
            }
          })
          .catch(error => console.log(error))
        listOptions = this.dataSearch.map((item: Record<string, any>) => {
          return {
            value: `${item.id}`,
            label: `${item.customerCifCode ? item.customerCifCode : ''}${item.customerCifCode && item.customerFullName ? ' - ' : ''}${
              item.customerFullName ? item.customerFullName : ''
            }`
          }
        })
        break
      case 'relationship':
        listOptions = []
        break
    }

    this.loading = false
    return listOptions
  }
  dataPropCustomer: any = {
    title: '',
    id: '',
    code: ''
  }
  handleAddCustomer(): void {
    // console.log('2')
    // if (this.valueRelationship == '1' || this.valueRelationship == '4') {
    //   this.checkCustomerType = true
    // } else {
    //   this.checkCustomerType = false
    // }
    if (this.checkCustomerType) {
      EventBus.$emit('checkNullCif')
    } else {
      EventBus.$emit('checkNullCif2')
    }
    this.dataPropCustomer.code = 'add'
    this.dataPropCustomer.title = 'Thêm mới khách hàng'
    this.setOpenPopup({
      popupName: 'addcustomer-popup',
      isOpen: true
    })
  }
  handleClose(): void {
    this.valueSelect = ''
    this.valueRelationship = ''
    this.dataSearch = []
    this.selectOptions = []
    this.dataPropCustomer = {
      title: '',
      id: '',
      code: ''
    }
    this.setOpenPopup({
      popupName: 'audit-action',
      isOpen: false
    })
    this.$emit('close')
  }

  handleSave(): void {
    let listBankContractNo = this.data.bankContractNo
    let listContractNo = this.data.contractNo
    let contractArray: Array<any> = []
    forEach(this.dataSelect, value => {
      contractArray.push(value.contractNo)
    })
    let params: Record<string, any> = {}
    let checkValidate = true
    switch (this.data.code) {
      case 'advisoryStaff':
        if (!this.valueSelect) checkValidate = false
        params = {
          bankContractNoArray: listBankContractNo,
          contractNoArray: listContractNo,
          salesMemberId: this.valueSelect
        }
        break
      case 'introStaff':
        if (!this.valueSelect) checkValidate = false
        params = {
          bankContractNoArray: listBankContractNo,
          contractNoArray: listContractNo,
          refMemberId: this.valueSelect
        }
        break
      case 'customerCif':
        if (!this.valueSelect) checkValidate = false
        params = {
          bankContractNoArray: listBankContractNo,
          contractNoArray: listContractNo,
          customerId: this.valueSelect
        }
        break
      case 'relationship':
        if (!this.valueRelationship || this.valueRelationship == '') checkValidate = false
        params = {
          bankContractNoArray: listBankContractNo,
          contractNoArray: listContractNo,
          customerType: this.valueRelationship
        }
        break
    }
    if (checkValidate) {
      params.handOverDate = null
      console.log('prams', params)

      this.api
        .updateAuditSelect(params)
        .then(() => {
          this.$message.success('Cập nhật thành công!')
          this.$emit('update')
        })
        .catch((err: any) => {
          this.$message.error(err.message)
        })
      this.handleClose()
    } else {
      this.$message.error('Bạn chưa ' + this.data.title?.toLowerCase())
    }
  }
  saveCustomer(customer: Record<string, any>): void {
    if (customer && customer.id > 0) {
      this.selectOptions = [
        {
          value: customer.id,
          label: `${customer.customerCifCode} - ${customer.customerFullName}`
        }
      ]
      this.valueSelect = customer.id
      this.emailSelect = customer.customerEmail
      this.phoneSelect = customer.customerMobilePhone
    }
  }
  created(): void {
    this.api = getRepository('audit', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiMember = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiCustomer = getRepository('customer', {
      districtId: this.user?.districts?.[0].id
    })
  }
  getRelationship(code: string): string {
    return RELATIONSHIP_TYPE[code]
  }
  handleClearData(): void {
    this.queryDrop.i = 0
    this.queryDrop.search = ''

    this.loadDataSearch(this.data.code)
      .then((res: any) => {
        this.listOptions = res
        this.selectOptions = this.listOptions.filter((item: Record<string, any>) => {
          return item.label.toLowerCase().indexOf(this.queryDrop.search.toLowerCase()) > -1
        })
      })
      .catch(err => {
        console.log(err)
      })
  }
}
