  checkInfo(arr: Array<Record<string, any>>): number {
    let sum = 0
    const listFilter = [...new Map(arr.map((item: Record<string, any>) => [item['bankContractNo'], item])).values()]
    listFilter.map(item => {
      let check = false
      let checkSale = item.salesMemberId ? true : false
      let checkRelationShip = item.customerType ? true : false
      if (!checkSale || !checkRelationShip || ((item.customerType == '2' || item.customerType == '3') && (!item.customerId || isNaN(item.customerId)))) {
        check = true
      } else {
        check = false
      }
      if (check) {
        sum++
      }
    })
    return sum
  }
  async handleConfirm(): Promise<void> {
    if (this.checkInfo(this.dataSelect) > 0) {
      this.$message.warning(`Có ${this.checkInfo(this.dataSelect)} hợp đồng chưa được gán đủ thông tin`)
      return
    }
    this.loadingButton = true
    if (this.query) {
      this.query.newAuditStatus = '3'
      this.query.limit = ''
      this.query.page = ''
    }

    if (this.dataSelect && this.dataSelect.length > 0) {
      const bankContractNo = this.dataSelect.map((item: any) => item.bankContractNo).join(',')
      const contractNo = this.dataSelect.map((item: any) => item.contractNo).join(',')
      await this.api
        ?.updateAuditSelect({
          bankContractNoArray: bankContractNo,
          contractNoArray: contractNo,
          auditStatus: '3'
        })
        .then(() => {
          this.tableData = []
          this.$message({
            message: 'Yêu cầu duyệt thành công',
            type: 'success'
          })
        })
        .catch(err => {
          this.$message.warning(err.message)
        })
    } else {
      await this.api
        ?.updateAuditFilter(this.query)
        .then(() => {
          this.tableData = []
          this.$message({
            message: 'Yêu cầu duyệt thành công',
            type: 'success'
          })
        })
        .catch(error => {
          this.$message.warning(error.message)
        })
    }
    this.setOpenPopup({
      popupName: 'auditting-popup',
      isOpen: false
    })
    this.loadingButton = false
    this.$emit('update')
  }
