




























































































































































































































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import { AuditEntry } from '@/services/entities/audit'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'
import getRepository from '@/services'
import AuditRepository from '@/services/repositories/audit'
import { namespace } from 'vuex-class'
import ContractRepository from '@/services/repositories/contract'
import EmployeeRepository from '@/services/repositories/employee'
import CustomerRepository from '@/services/repositories/customer'
import AddCustomer from './AddCustomer.vue'
import { RELATIONSHIP_TYPE } from '@/configs'
import { debounce, filter, trim } from 'lodash'
import EventBus from '@/utils/eventBus'
const beUser = namespace('beUser')

@Component({ components: { AddCustomer } })
export default class AuditPopup extends Mixins(PopupMixin, DistrictMixin) {
  @Prop() dataPopup: any
  @Prop() propCustomerType: any
  @Prop() params?: Record<string, any>
  @beUser.State('user') user?: Record<string, any>
  data: any
  auditData: Array<AuditEntry> = []
  api?: AuditRepository
  apiContract?: ContractRepository
  apiCustomer?: CustomerRepository
  apiMember?: EmployeeRepository
  valueNote: string | any = ''
  index = 0
  loading = false
  show = false
  valueStaffCode = ''
  optionsRelationship = []
  querySale = {
    i: 0,
    ps: 10,
    search: ''
  }
  queryCustomer = {
    i: 0,
    ps: 10,
    search: ''
  }
  queryRef = {
    i: 0,
    ps: 10,
    search: ''
  }
  isBtnLoading = {
    close: false,
    continue: false,
    req: false,
    prev: false,
    next: false
  }
  listRef: any
  selectRefOptions: Array<Record<string, any>> = []
  valueSelectRef: string | any = ''
  valueSelectCustomer: string | any = ''
  valueSelectSale: string | any = ''
  listCustomer: any
  arrayCustomer: Array<Record<string, any>> = []
  listSale: any
  selectSaleOptions: Array<Record<string, any>> = []
  consultantMemberIdArray: any = []
  customerType: any = ''
  showDetail = false
  oneAudit: AuditEntry = {}
  hiddenFooterPopup = false
  disableButton = {
    prev: false,
    next: false
  }
  showInfo = {
    ref: true,
    customer: true,
    sale: true
  }
  field = {
    sale: false,
    ref: false,
    customer: false,
    relationship: false
  }
  dataPropCustomer: any = {
    title: '',
    id: '',
    code: ''
  }

  isLoadmore = true

  get placeholderNote(): string {
    if (this.$route.params.status == 'da-duyet' || this.$route.params.status == 'cho-duyet' || this.$route.params.status == 'yeu-cau-hieu-chinh') {
      return ''
    }
    return 'Nhập ghi chú'
  }

  get getHidden(): boolean {
    if (this.$route.params.status == 'da-khop' || this.$route.name == 'da-khop') {
      return this.checkPemission('audit', ['edit-input'])
    }
    return false
  }

  get disableSelect(): boolean {
    if (this.$route.params.status == 'da-khop' || this.$route.name == 'da-khop') {
      return !this.checkPemission('audit', ['edit-input'])
    }
    return true
  }
  handleClearCustomer(): void {
    this.showInfo.customer = false
  }
  handleClearRef(): void {
    this.showInfo.ref = false
  }
  handleClearSale(): void {
    this.showInfo.sale = false
  }
  checkCustomerType = false
  handleAddCustomer(): void {
    this.dataPropCustomer.code = 'add'
    if (this.oneAudit.customerType == '1' || this.oneAudit.customerType == '4') {
      this.checkCustomerType = true
      //ban su kien sang AddCustomer
      EventBus.$emit('checkNullCif')
    } else {
      this.checkCustomerType = false
      EventBus.$emit('checkNullCif2')
    }
    this.setOpenPopup({
      popupName: 'addcustomer-popup',
      isOpen: true
    })
  }
  handleChangeRelationship(): void {
    this.field.relationship = false
  }
  handleChangeCustomer(): void {
    this.showInfo.customer = true
    this.field.customer = false
  }
  handleChangeSale(value: Record<string, any>): void {
    const item = filter(this.selectSaleOptions, (elm: Record<string, any>) => elm.id === value)
    if (item.length) {
      this.oneAudit.salesMobilePhone = item[0].phone
      this.oneAudit.salesEmail = item[0].email
    }
    this.showInfo.sale = true
    this.field.sale = false
  }
  handleChangeRef(value: number | any): void {
    const item = filter(this.selectRefOptions, (elm: Record<string, any>) => elm.id === value)
    if (item.length) {
      this.oneAudit.refMobilePhone = item[0].phone
      this.oneAudit.refEmail = item[0].email
    }
    this.showInfo.ref = true
    this.field.ref = false
  }
  remoteCustomer(query: string): void {
    this.queryCustomer.search = query
    this.queryCustomer.i = 0
    this.queryCustomer.ps = 10
    this.isLoadmore = false
    this.debounceRemoteCus()
  }

  debounceRemoteCus = debounce(() => {
    this.handleSearchCus()
  }, 600)

  handleSearchCus(): void {
    const temp = {
      ...this.queryCustomer,
      search: trim(this.queryCustomer.search),
      page: this.queryCustomer.i,
      size: this.queryCustomer.ps
    }
    this.apiCustomer?.getCustomers(temp).then(res => {
      this.arrayCustomer = res.data.content
      setTimeout(() => {
        this.isLoadmore = true
      }, 1000)
    })
  }

  remoteRef(query: string): void {
    this.queryRef.i = 0
    this.queryRef.ps = 10
    this.queryRef.search = trim(query)
    this.isLoadmore = false
    this.debounceRemoteRef()
  }

  debounceRemoteRef = debounce(() => {
    this.handleSearchRef()
  }, 600)

  handleSearchRef(): void {
    const temp = {
      ...this.queryRef,
      search: trim(this.queryRef.search),
      groupCode: 'INSURANCE'
    }
    this.apiMember?.getEmployeeList(temp).then(res => {
      this.selectRefOptions = res.data
      setTimeout(() => {
        this.isLoadmore = true
      }, 1000)
    })
  }

  remoteSale(query: string): void {
    this.querySale.i = 0
    this.querySale.ps = 10
    this.querySale.search = trim(query)
    this.isLoadmore = false
    this.debounceRemoteSale()
  }

  debounceRemoteSale = debounce(() => {
    this.handleSearchSale()
  }, 600)

  handleSearchSale(): void {
    const temp = {
      ...this.querySale,
      search: trim(this.querySale.search),
      groupCode: 'INSURANCE'
    }
    this.apiMember?.getEmployeeList(temp).then(res => {
      this.selectSaleOptions = res.data
      setTimeout(() => {
        this.isLoadmore = true
      }, 1000)
    })
  }

  checkExitsArray(id: number, arr: Array<Record<string, any>>): number {
    return filter(arr, elm => elm.id === id).length
  }
  async getListSaleMember(): Promise<void> {
    const temp = {
      ...this.querySale,
      groupCode: 'INSURANCE'
    }
    await this.apiMember?.getEmployeeList(temp).then(res => {
      this.selectSaleOptions = [...this.selectSaleOptions, ...res.data]
      const item: Record<string, any> = {
        memberNo: this.oneAudit.salesMemberNo,
        fullName: this.oneAudit.salesFullName,
        id: this.oneAudit.salesMemberId,
        phone: this.oneAudit.salesMobilePhone,
        refWorkPhone: this.oneAudit.salesWorkPhone,
        email: this.oneAudit.salesEmail
      }
      if (item.id) {
        if (!this.checkExitsArray(item.id, this.selectSaleOptions)) {
          //@ts-ignore
          this.selectSaleOptions.unshift(item)
        }
      }
    })
  }

  async getListRefMember(): Promise<void> {
    const temp = {
      ...this.queryRef,
      groupCode: 'INSURANCE'
    }
    await this.apiMember
      ?.getEmployeeList(temp)
      .then(res => {
        this.selectRefOptions = [...this.selectRefOptions, ...res.data]
        const item: Record<string, any> = {
          memberNo: this.oneAudit.refMemberNo,
          fullName: this.oneAudit.refFullName,
          id: this.oneAudit.refMemberId,
          phone: this.oneAudit.refMobilePhone,
          refWorkPhone: this.oneAudit.refWorkPhone,
          email: this.oneAudit.refEmail
        }
        if (item.id) {
          if (!this.checkExitsArray(item.id, this.selectRefOptions)) {
            //@ts-ignore
            this.selectRefOptions.unshift(item)
          }
        }
      })
      .catch(err => console.log(err))
  }

  async getListCifMember(): Promise<void> {
    await this.apiCustomer
      ?.getCustomers({
        search: trim(this.queryCustomer.search),
        page: this.queryCustomer.i,
        size: this.queryCustomer.ps
      })
      .then(list => {
        this.arrayCustomer = [...this.arrayCustomer, ...list.data.content]
        const item: Record<string, any> = {
          customerCifCode: this.oneAudit.customerCifCode,
          customerFullName: this.oneAudit.customerFullName,
          id: this.oneAudit.customerId
        }
        if (item.id) {
          if (!this.checkExitsArray(item.id, this.arrayCustomer)) {
            //@ts-ignore
            this.arrayCustomer.unshift(item)
          }
        } else {
          this.oneAudit.customerId = this.oneAudit.customerFullName
        }
      })
  }
  loadMoreCustomer(): void {
    if (!this.isLoadmore) {
      return
    }
    this.queryCustomer.i += this.queryCustomer.ps
    this.getListCifMember()
  }
  loadMoreSale(): void {
    if (!this.isLoadmore) {
      return
    }
    this.querySale.i += this.querySale.ps
    this.getListSaleMember()
  }
  loadMoreRef(): void {
    if (!this.isLoadmore) {
      return
    }
    this.queryRef.i += this.queryRef.ps
    this.getListRefMember()
  }
  handleToggleDetail(): void {
    this.showDetail = !this.showDetail
  }

  async handleOpen(): Promise<void> {
    this.index = 0
    // this.show = true
    this.oneAudit = this.dataPopup.data[this.index]
    this.isBtnLoading.next = false
    this.isBtnLoading.req = false
    this.isBtnLoading.continue = false
    if (this.params) {
      this.hiddenFooterPopup = this.dataPopup.data.length > 1 ? false : true
    }
    this.checkDisableButton()
    await this.getListSaleMember()
    await this.getListRefMember()
    await this.getListCifMember()
    this.setData()
    // this.show = false
  }

  setData(): void {
    const key = 'bankContractNo'
    const listFilter = [...new Map(this.dataPopup.data.map((item: Record<string, any>) => [item[key], item])).values()]
    this.dataPopup.data = listFilter
    this.data = [...this.dataPopup.data]
    this.oneAudit = this.dataPopup.data[this.index]
    if (this.oneAudit) {
      this.optionsRelationship = this.propCustomerType.data
      this.valueStaffCode =
        this.oneAudit && (this.oneAudit.consultantNo || this.oneAudit.consultantName)
          ? `${this.oneAudit.consultantNo ? `${this.oneAudit.consultantNo} - ` : ''}${this.oneAudit.consultantName}`
          : ''
      const elementCus: Record<string, any> = {
        customerCifCode: this.oneAudit.customerCifCode,
        customerFullName: this.oneAudit.customerFullName,
        id: this.oneAudit.customerId
      }
      if (elementCus.id) {
        if (!this.checkExitsArray(elementCus.id, this.arrayCustomer)) {
          //@ts-ignore
          this.arrayCustomer.unshift(elementCus)
        }
      } else {
        this.oneAudit.customerId = this.oneAudit.customerFullName
      }
      const elementSale: Record<string, any> = {
        memberNo: this.oneAudit.salesMemberNo,
        fullName: this.oneAudit.salesFullName,
        id: this.oneAudit.salesMemberId,
        phone: this.oneAudit.salesMobilePhone,
        refWorkPhone: this.oneAudit.salesWorkPhone,
        email: this.oneAudit.salesEmail
      }
      if (elementSale.id) {
        if (!this.checkExitsArray(elementSale.id, this.selectSaleOptions)) {
          this.selectSaleOptions.unshift(elementSale)
        }
      }
      const element: Record<string, any> = {
        memberNo: this.oneAudit.refMemberNo,
        fullName: this.oneAudit.refFullName,
        id: this.oneAudit.refMemberId,
        phone: this.oneAudit.refMobilePhone,
        refWorkPhone: this.oneAudit.refWorkPhone,
        email: this.oneAudit.refEmail
      }
      if (element.id) {
        if (!this.checkExitsArray(element.id, this.selectRefOptions)) {
          //@ts-ignore
          // this.selectRefOptions = this.selectRefOptions.filter(item => item.id != element.id)
          this.selectRefOptions.unshift(element)
        }
      }
      const keyItem = 'id'
      this.arrayCustomer = [...new Map(this.arrayCustomer.map((item: Record<string, any>) => [item[keyItem], item])).values()]
      this.selectSaleOptions = [...new Map(this.selectSaleOptions.map((item: Record<string, any>) => [item[keyItem], item])).values()]
      this.selectRefOptions = [...new Map(this.selectRefOptions.map((item: Record<string, any>) => [item[keyItem], item])).values()]
    }
  }
  getNameCustomer(customerCifCode: any, customerFullName: any): void {
    let name: any = ''
    if (customerCifCode) {
      name = customerCifCode + ' - ' + customerFullName
    } else {
      name = customerFullName
    }
    return name
  }
  // arrayCustomer: any = []
  saveCustomer(customer: Record<string, any>): void {
    // if (customer && customer.id > 0) {
    //   this.arrayCustomer = [customer]
    //   this.oneAudit.customerId = customer.id
    //   this.oneAudit.customerFullName = customer.customerFullName
    //   this.oneAudit.customerCifCode = customer.customerCifCode
    //   this.oneAudit.address = customer.address
    // }
    if (customer && customer.id > 0) {
      this.arrayCustomer.unshift({
        id: customer.id,
        customerCifCode: customer.customerCifCode,
        customerFullName: customer.customerFullName,
        address: customer.address
      })
      this.oneAudit.customerId = customer.id
    }
    console.log('array', this.arrayCustomer)
  }

  checkDisableButton(): void {
    this.disableButton = {
      prev: this.index <= 0 && this.params && this.params.page < 2 ? true : false,
      next: this.index >= this.dataPopup.data.length - 1 ? true : false
    }
  }

  resetQuery(): void {
    this.querySale = {
      i: 0,
      ps: 10,
      search: ''
    }
    this.queryCustomer = {
      i: 0,
      ps: 10,
      search: ''
    }
    this.queryRef = {
      i: 0,
      ps: 10,
      search: ''
    }
  }
  checkNull(): void {
    if (!this.oneAudit.customerId) {
      this.oneAudit.customerCifCode = null
      this.oneAudit.customerFullName = null
    }
    if (!this.oneAudit.refMemberId) {
      this.oneAudit.refMemberNo = null
      this.oneAudit.refFullName = null
      this.oneAudit.refMobilePhone = null
      this.oneAudit.refWorkPhone = null
      this.oneAudit.refEmail = null
      this.oneAudit.refLocationId = null
      this.oneAudit.refLocationName = null
      this.oneAudit.refTransactionOfficeType = null
    }
    if (!this.oneAudit.salesMemberId) {
      this.oneAudit.salesMemberNo = null
      this.oneAudit.salesFullName = null
      this.oneAudit.salesMobilePhone = null
      this.oneAudit.salesWorkPhone = null
      this.oneAudit.salesEmail = null
    }
  }
  handleClose(): void {
    this.index = 0
    this.field = {
      sale: false,
      ref: false,
      customer: false,
      relationship: false
    }
    this.show = false
    this.selectSaleOptions = []
    this.selectRefOptions = []
    this.arrayCustomer = []
    this.resetQuery()
  }
  async handleSaveClose(): Promise<void> {
    this.isBtnLoading.close = true
    if ((!this.oneAudit.customerId || isNaN(this.oneAudit.customerId)) && (this.oneAudit.customerType == '2' || this.oneAudit.customerType == '3')) {
      this.field.customer = true
      this.isBtnLoading.close = false
      return
    } else {
      this.field.customer = false
    }
    if (!this.oneAudit.salesMemberId) {
      this.field.sale = true
      this.isBtnLoading.close = false
      return
    } else {
      this.field.sale = false
    }
    if (!this.oneAudit.customerType) {
      this.field.relationship = true
      this.isBtnLoading.close = false
      return
    } else {
      this.field.relationship = false
    }
    // if (this.oneAudit.customerType == 2 || this.oneAudit.customerType == 3) {
    //   if (isNaN(this.oneAudit.customerId)) {
    //     this.field.customer = true
    //     this.isBtnLoading.close = false
    //     return
    //   }
    // } else {
    //   this.field.customer = false
    // }
    if (!this.oneAudit.salesMemberId) {
      this.field.sale = true
      this.isBtnLoading.close = false
      return
    } else {
      this.field.sale = false
    }

    if (this.oneAudit) {
      if (isNaN(this.oneAudit.customerId)) {
        this.oneAudit.customerId = null
      }
      this.checkNull()
      const contractNo = this.oneAudit.bankContractNo
      // if (this.oneAudit.salesMemberId && this.oneAudit.customerType) {
      // if (!this.$route.params.status) {
      // if (this.oneAudit.customerType == 1 || this.oneAudit.customerType == 4) {
      //   this.oneAudit.auditStatus = 2
      // }
      // if (this.oneAudit.customerType == 2 || this.oneAudit.customerType == 3) {
      //   if (this.oneAudit.customerId) {
      //     this.oneAudit.auditStatus = 2
      //   }
      // }
      // }
      // }

      await this.apiContract
        ?.updateOneAudit(contractNo, this.oneAudit)
        .then(() => {
          this.$message.success('Cập nhập thành công')
        })
        .catch(err => {
          this.$message.error(err.message)
        })
      this.setOpenPopup({
        popupName: 'audit',
        isOpen: false
      })
    }
    this.field = {
      sale: false,
      ref: false,
      customer: false,
      relationship: false
    }

    this.$emit('update')
    this.isBtnLoading.close = false
  }
