  getRelationship(code: string): string {
    return RELATIONSHIP_TYPE[code]
  }
  async handlePrev(): Promise<void> {
    this.checkDisableButton()
    this.field = {
      sale: false,
      ref: false,
      customer: false,
      relationship: false
    }
    if (this.index > 0) {
      this.index--
    }
    if (this.params && this.params.page > 1 && this.index < 1) {
      this.isBtnLoading.prev = true
      await this.$emit('handlePrev')
      if (this.dataPopup.count) {
        this.index = this.index + this.dataPopup.count
      }
    }
    this.isBtnLoading.prev = false
    this.setData()
  }
  async handleNext(): Promise<void> {
    if (!this.isBtnLoading.continue && !this.isBtnLoading.req) {
      this.isBtnLoading.next = true
    } else {
      this.isBtnLoading.req = false
      this.isBtnLoading.continue = false
    }
    this.checkDisableButton()
    if ((!this.oneAudit.customerId || isNaN(this.oneAudit.customerId)) && (this.oneAudit.customerType == '2' || this.oneAudit.customerType == '3')) {
      this.field.customer = true
      this.resetBtnLoading()
      return
    } else {
      this.field.customer = false
    }
    if (!this.oneAudit.customerType) {
      this.field.relationship = true
      this.resetBtnLoading()
      return
    } else {
      this.field.relationship = false
    }
    if (!this.oneAudit.salesMemberId) {
      this.field.sale = true
      this.resetBtnLoading()
      return
    } else {
      this.field.sale = false
    }
    if (this.oneAudit) {
      if (isNaN(this.oneAudit.customerId)) {
        this.oneAudit.customerId = null
      }
      this.checkNull()
      // console.log('one', this.oneAudit)
      // console.log('inde', this.dataPopup.data[this.index])
      // console.log(JSON.stringify(this.oneAudit) === JSON.stringify(this.data[this.index]))

      // if (JSON.stringify(this.oneAudit) === JSON.stringify(this.dataPopup.data[this.index])) {
      //   return JSON.stringify(this.oneAudit) === JSON.stringify(this.dataPopup.data[this.index])
      // }
      const contractNo = this.oneAudit.bankContractNo
      await this.apiContract
        ?.updateOneAudit(contractNo, this.oneAudit)
        .then(() => {
          this.$message.success('Cập nhập thành công')
          if (this.oneAudit.auditStatus == '3') {
            const newList = this.dataPopup.data.filter((item: Record<string, any>) => item.bankContractNo != this.oneAudit.bankContractNo)
            this.dataPopup.data = newList
          }
          if (this.dataPopup.data.length < 1 || this.dataPopup.action == 'detail') {
            this.setOpenPopup({
              popupName: 'audit',
              isOpen: false
            })
          }

          this.isBtnLoading.next = false
        })
        .catch(() => {
          this.$message.error('Cập nhập thất bại')
          this.isBtnLoading.next = false
        })
    }

    this.index++
    if (this.index > 0) {
      this.disableButton.prev = false
    } else {
      this.disableButton.prev = true
    }
    if (this.index >= this.dataPopup.data.length - 2 && this.dataPopup.action != 'detail') {
      this.$emit('handleNext')
    }
    this.field = {
      sale: false,
      ref: false,
      customer: false,
      relationship: false
    }

    if (this.dataPopup.action != 'detail') {
      this.setData()
    }
    this.$emit('update')
  }
  resetBtnLoading(): void {
    this.isBtnLoading.next = false
    this.isBtnLoading.continue = false
    this.isBtnLoading.req = false
    this.isBtnLoading.close = false
  }
  created(): void {
    this.api = getRepository('audit', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiMember = getRepository('employee', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiCustomer = getRepository('customer', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
