





















































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import AuditRepository from '@/services/repositories/audit'
import getRepository from '@/services'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import { PaginationInterface } from '@/interface'
import ContractRepository from '@/services/repositories/contract'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class TestPopup extends Mixins(PopupMixin) {
  @Prop({ default: 0 }) pageNumber?: number
  @Prop({ default: false }) lastItem?: boolean
  ipAmount: any
  totalAmount: any
  numberContract: any
  @Prop({ required: true, type: Object, default: {} }) query!: Record<string, any>
  @Prop({ default: [] }) dataSelect?: Array<Record<string, any>>
  @beUser.State('user') user?: Record<string, any>
  total: number | string = 0
  api?: AuditRepository
  apiContract?: ContractRepository
  data: Record<string, any> = {}
  isLoading = false
  tableData = []
  listParent: any = []
  listPop: any = []
  responseList: any = []
  newDataSelect: any = []
  listDetailContract: any = []
  listSubContract: any = []
  dataNew: any = []
  summary: Record<string, any> = {}
  saleAmount: any
  pageQuery: PaginationInterface = {
    page: 1,
    search: '',
    limit: 20,
    total: 0,
    orderByClause: ''
  }
  selectQuery: PaginationInterface = {
    page: 1,
    search: '',
    limit: 10,
    total: 0,
    orderByClause: ''
  }
  newquery: any = {
    areaIdArray: [],
    branchIdArray: [],
    transOfficeIdArray: [],
    salesMemberIdArray: [],
    consultantMemberIdArray: [],
    refMemberIdArray: [],
    productIdArray: [],
    newAuditStatus: 3,
    paymentPeriod: '',
    currentContractYear: '',
    currentFeeYear: '',
    numOfContractYear: '',
    salesChannel: '',
    consultantType: '',
    customerType: '',
    contractStatus: '',
    expireStatus: '',
    auditStatus: 2,
    fromApplyFormDate: '',
    toApplyFormDate: '',
    fromIssueDate: '',
    toIssueDate: '',
    fromLastPayDate: '',
    toLastPayDate: '',
    fromExpireDate: '',
    toExpireDate: '',
    fromBasicAmount: '',
    toBasicAmount: '',
    fromExtraAmount: '',
    toExtraAmount: '',
    fromTotalAmount: '',
    toTotalAmount: ''
  }
  params = {
    searchType: 'CONTRACT',
    auditStatus: '2',
    page: 1,
    limit: 20
  }
  dataInput = {
    keywordString: '',
    areaIdArray: [],
    branchIdArray: '',
    transOfficeIdArray: '',
    salesMemberIdArray: '',
    consultantMemberIdArray: '',
    refMemberIdArray: '',
    productIdArray: '',
    paymentPeriod: '',
    currentContractYear: '',
    currentFeeYear: '',
    numOfContractYear: '',
    salesChannel: '',
    consultantType: '',
    customerType: '',
    contractStatus: '',
    expireStatus: '',
    auditStatus: 2,
    fromApplyFormDate: '',
    toApplyFormDate: '',
    fromIssueDate: '',
    toIssueDate: '',
    fromLastPayDate: '',
    toLastPayDate: '',
    fromExpireDate: '',
    toExpireDate: '',
    fromBasicAmount: '',
    toBasicAmount: '',
    fromExtraAmount: '',
    toExtraAmount: '',
    fromTotalAmount: '',
    toTotalAmount: '',
    page: 1,
    limit: 20
  }
  newData = {}
  pageInfor = {
    total: '',
    page: 1,
    limit: 20
  }
  isBtnLoading = false

  async handleConfirm(): Promise<void> {
    this.isBtnLoading = true

    if (this.query) {
      this.query.newAuditStatus = this.$route.name == 'yeu-cau-hieu-chinh' ? '8' : '3'
      this.query.limit = ''
      this.query.page = ''
    }
    if (this.dataSelect && this.dataSelect.length > 0) {
      const bankContractNo = this.dataSelect.map((item: any) => item.bankContractNo).join(',')
      const contractNo = this.dataSelect.map((item: any) => item.contractNo).join(',')
      await this.api
        ?.updateAuditSelect({
          bankContractNoArray: bankContractNo,
          auditStatus: 5,
          contractNoArray: contractNo
        })
        .then(() => {
          // this.dataSelect = []
          this.tableData = []
          this.$message({
            message: this.$route.name == 'yeu-cau-hieu-chinh' ? 'Phê duyệt hiệu chỉnh thành công' : 'Yêu cầu hiệu chỉnh thành công',
            type: 'success'
          })
        })
        .catch(() => {
          this.$message({
            message: this.$route.name == 'yeu-cau-hieu-chinh' ? 'Phê duyệt hiệu chỉnh thất bại' : 'Yêu cầu hiệu chỉnh thất bại',
            type: 'warning'
          })
        })
    } else {
      await this.api
        ?.updateAuditFilter(this.query)
        .then(() => {
          // this.dataSelect = []
          this.tableData = []
          this.$message({
            message: this.$route.name == 'yeu-cau-hieu-chinh' ? 'Phê duyệt hiệu chỉnh thành công' : 'Yêu cầu hiệu chỉnh thành công',
            type: 'success'
          })
        })
        .catch(() => {
          this.$message({
            message: this.$route.name == 'yeu-cau-hieu-chinh' ? 'Phê duyệt hiệu chỉnh thất bại' : 'Yêu cầu hiệu chỉnh thất bại',
            type: 'warning'
          })
        })
    }
    // setTimeout(() => {
    this.setOpenPopup({
      popupName: 'edit-popup',
      isOpen: false
    })
    // })
    this.isBtnLoading = false

    this.$emit('update')
  }
  handleSelection(): void {
    return
  }
  handleSizeChange(value: number): void {
    this.params.page = 1
    this.params.limit = value
    this.pageQuery.page = 1
    this.pageQuery.limit = value
    this.selectQuery.page = 1
    this.selectQuery.limit = value
    this.setData()
  }
  handleCurrentChange(value: number): void {
    this.params.page = value
    this.setData()
  }
  checkLength(value: string): string {
    if (value.split(' ').length > 5) {
      return value.split(' ').splice(0, 4).join(' ') + '...'
    }
    return value
  }

  setData(): void {
    this.isLoading = true
    this.query.limit = this.params.limit
    this.query.page = this.params.page
    if (this.dataSelect && this.dataSelect.length > 0) {
      // this.selectQuery.total = this.responseList?.length
      if (this.selectQuery.page == 1 && this.selectQuery.limit == 10) {
        this.newDataSelect = this.responseList?.slice(0, 10)
      } else if (this.selectQuery.page == 1 && this.selectQuery.limit != 1) {
        this.newDataSelect = this.responseList?.slice(0, this.selectQuery.limit)
      } else if (this.selectQuery.page != 1) {
        this.newDataSelect = this.responseList?.slice(this.selectQuery.limit * (this.selectQuery.page - 1), this.selectQuery.limit * this.selectQuery.page)
      }
    }
    this.apiContract
      ?.getData(this.query)
      .then((list: Record<string, any>) => {
        this.data = list
        this.tableData = this.data.data
        this.isLoading = false
        this.pageInfor.total = list.total
      })
      .catch(() => (this.isLoading = false))

    this.apiContract
      ?.getContractCount(this.query)
      .then(count => {
        this.pageQuery.total = count.data
        this.isLoading = false
      })
      .catch(() => (this.isLoading = false))
  }
  // mounted(): void {
  //   this.setData(this.params)
  // }

  init(): void {
    this.isLoading = false
  }
  created(): void {
    this.api = getRepository('audit', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.init()
    this.isLoading = false
  }
  getDataSumary(): void {
    if (this.dataSelect && this.dataSelect.length > 0) {
      const bankContractNo = this.dataSelect.map((item: any) => item.bankContractNo).join(',')
      const contractNo = this.dataSelect.map((item: any) => item.contractNo).join(',')
      this.apiContract
        ?.getSalaryContractSelect({
          bankContractNoArray: bankContractNo,
          contractNoArray: contractNo
        })
        .then((response: any) => {
          this.summary = response.data
        })
        .catch(() => (this.isLoading = false))
    } else {
      this.apiContract
        ?.getSalaryContract(this.query)
        .then((response: any) => {
          this.summary = response.data
        })
        .catch(() => (this.isLoading = false))
    }
  }
  getNumberSubContract(): void {
    this.dataNew = this.dataSelect?.slice(this.dataSelect.length - 1)
    this.apiContract
      ?.getDataDetail(this.dataNew[0].bankContractNo)
      .then((response: any) => {
        // this.listDetailContract = response
        this.listSubContract = response.subContracts
        // this.responseList = this.dataSelect?.concat(this.listSubContract)
        if (this.pageNumber == this.dataSelect?.length) {
          for (const id in this.listSubContract) {
            this.listParent.push(this.dataNew[0])
          }
          this.listPop = this.dataSelect?.slice(0, this.dataSelect.length - 1)
          this.responseList = this.listPop?.concat(this.listParent)
          this.selectQuery.total = this.responseList?.length
          this.newDataSelect = this.responseList?.slice(0, 10)
        } else if (this.lastItem == true) {
          for (const id in this.listSubContract) {
            this.listParent.push(this.dataNew[0])
          }
          this.listPop = this.dataSelect?.slice(0, this.dataSelect.length - 2)
          this.responseList = this.listPop?.concat(this.listParent)
          this.selectQuery.total = this.responseList?.length
          this.newDataSelect = this.responseList?.slice(0, 10)
        } else {
          this.responseList = this.dataSelect
          this.selectQuery.total = this.responseList?.length
          this.newDataSelect = this.responseList?.slice(0, 10)
        }
      })
      .catch((err: any) => console.log(err))
  }
  handleOpen(): void {
    if (this.dataSelect && this.dataSelect.length > 0) {
      this.getNumberSubContract()
    }
    this.getDataSumary()
    this.setData()
    this.newquery = {
      ...this.query
    }
  }
  handleClose(): void {
    // this.dataSelect = []
    this.listParent = []
    // this.selectQuery.total = 0
    // this.selectQuery.page = 1
    // this.selectQuery.limit = 20
    this.$emit('reset')
  }
  handleFilter(): void {
    return
  }
  filters(): void {
    // console.log(1)
  }
}
