










































































import { Component, Mixins, Prop } from 'vue-property-decorator'
import AuditRepository from '@/services/repositories/audit'
import getRepository from '@/services'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import { PaginationInterface } from '@/interface'
import ContractRepository from '@/services/repositories/contract'
import { fill } from 'lodash'
const beUser = namespace('beUser')
@Component({ components: {} })
export default class ActionPopup extends Mixins(PopupMixin) {
  @Prop({ default: [] }) dataSelect?: Array<Record<string, any>>
  @Prop({ default: '' }) status?: string

  @beUser.State('user') user?: Record<string, any>
  total: number | string = 0
  api?: AuditRepository
  apiContract?: ContractRepository
  data: Record<string, any> = {}
  summary: Record<string, any> = {}
  isLoading = false

  pageQuery: PaginationInterface = {
    page: 1,
    limit: 20,
    total: 0
  }

  isBtnLoading = false

  dataTable: Array<Record<string, any>> = []
  orignalData: Array<Record<string, any>> = []

  get getTitle(): string {
    if (this.$route.name === 'cho-duyet') {
      if (this.status === 'reject') {
        return 'Từ chối'
      } else {
        return 'Phê duyệt'
      }
    } else {
      if (this.status === 'reject') {
        return 'Từ chối hiệu chỉnh'
      } else {
        return 'Phê duyệt hiệu chỉnh'
      }
    }
  }
  get getButtonName(): string {
    if (this.$route.name === 'cho-duyet') {
      if (this.status === 'reject') {
        return 'Từ chối'
      } else {
        return 'Phê duyệt'
      }
    } else {
      if (this.status === 'reject') {
        return 'Từ chối hiệu chỉnh'
      } else {
        return 'Phê duyệt hiệu chỉnh'
      }
    }
  }

  handleConfirm(): void {
    this.isBtnLoading = true
    const temp = {
      bankContractNoArray: this.dataSelect?.length ? this.dataSelect[0].bankContractNo : null,
      contractNoArray: this.dataSelect?.length ? this.dataSelect[0].contractNo : null,
      auditStatus:
        this.status === 'reject' && this.$route.name === 'cho-duyet'
          ? 6
          : this.status === 'approve' && this.$route.name === 'cho-duyet'
          ? 4
          : this.status === 'approve' && this.$route.name === 'yeu-cau-hieu-chinh'
          ? 8
          : 7
    }
    this.apiContract?.updateAuditSelect(temp).then(() => {
      if (this.$route.name === 'cho-duyet') {
        if (this.status === 'reject') {
          this.$message.success('Từ chối thành công')
        } else {
          this.$message.success('Phê duyệt thành công')
        }
      } else {
        if (this.status === 'reject') {
          this.$message.success('Từ chối hiệu chỉnh thành công')
        } else {
          this.$message.success('Phê duyệt hiệu chỉnh thành công')
        }
      }
      this.isBtnLoading = false
      this.setOpenPopup({
        popupName: 'action-popup',
        isOpen: false
      })
      this.$emit('update')
    })
  }

  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
  }

  async getDataSumary(): Promise<void> {
    try {
      const temp = {
        bankContractNoArray: this.dataSelect?.length ? this.dataSelect[0].bankContractNo : null,
        contractNoArray: this.dataSelect?.length ? this.dataSelect[0].contractNo : null
      }
      const result = await this.apiContract?.getSalaryContractSelect(temp)
      this.summary = result.data
    } catch (error) {
      console.log(error)
    }
  }
  handleOpen(): void {
    this.getDetail()
    this.getDataSumary()
  }
  getDetail(): void {
    this.isLoading = true
    this.apiContract
      //@ts-ignore
      ?.getDataDetail(this.dataSelect[0].bankContractNo)
      .then(res => {
        const numOfContract = res.subContracts.length
        let arraySub = fill(Array(numOfContract), res)

        for (let i = 0; i < numOfContract; i++) {
          // arraySub[i] = Object.assign(arraySub[i], arraySub[i].subContracts[i])
          arraySub[i] = {
            ...arraySub[i],
            ...res.subContracts[i]
          }
        }
        this.pageQuery.total = numOfContract
        this.orignalData = [...arraySub]
        this.handlePagination()
        this.isLoading = false
        this.$forceUpdate()
      })
      .catch(error => {
        this.isLoading = false
        console.log(error)
      })
  }

  handlePagination(): void {
    this.dataTable = this.orignalData.slice(
      this.pageQuery.page === 1 ? 0 : this.pageQuery.limit * (this.pageQuery.page - 1),
      this.pageQuery.page === 1 ? this.pageQuery.limit : this.pageQuery.page * this.pageQuery.limit
    )
  }

  handleSizeChange(size: number): void {
    this.pageQuery.limit = size
    this.handlePagination()
  }

  handleCurrentChange(val: number): void {
    this.pageQuery.page = val
    this.handlePagination()
  }

  resetQuery(): void {
    this.pageQuery = {
      page: 1,
      limit: 20,
      total: 0
    }
  }

  handleClose(): void {
    this.resetQuery()
  }
}
